import React, { useEffect, useState } from 'react'
import { Tweet } from 'react-twitter-widgets'
import axios from 'axios'

import './App.css'

const App = () => {
  const [tweets, setTweets] = useState([])
  const [loading, setLoading] = useState(true)
  const [activeCountry, setCountryActive] = useState('br')

  const handleTweets = pais => {
    axios.get(`http://localhost:8080/api/tweet/${pais}`).then(response => {
      setTweets(response.data)
      setLoading(false)
    })
  }

  useEffect(() => {
    handleTweets('br')
  }, [])

  const changeLanguage = country => {
    setLoading(true)
    setCountryActive(country)
    handleTweets(country)
  }

  return (
    <>
      <div className="menu">
        <label className={activeCountry === 'br' && 'active'} onClick={() => changeLanguage('br')}>Brasil</label>
        <label className={activeCountry === 'us' && 'active'} onClick={() => changeLanguage('us')}>Estados Unidos</label>
        <label className={activeCountry === 'uk' && 'active'} onClick={() => changeLanguage('uk')}>Inglaterra</label>
      </div>
      {loading ? <div className="loader" /> : (
        <div className="container-tweets">
          {tweets.map((tweet, index) => <Tweet key={index} tweetId={tweet} />)}
        </div>
      )}
    </>
  )
}

export default App
